.element-product-grid {
  .top-block {
    position: relative;
    background-color: #ffffff;
    border-bottom: 2px solid $color-hover;

    .top-block__heading {
      background-color: $color-hover;
      padding: 10px;
      margin-right: 10px;

      img {
        margin-right: 10px;
      }
      i {
        color: #ffffff;
        font-size: 18px;
      }
    }
    .heading {
      margin: 0 5px 0 0;
      font-weight: 400;
      text-transform: uppercase;

      a {
        color: #ffffff;
      }
    }
    .btn-icon-list-item-mobile {
      margin-right: 15px;
      font-size: 20px;
      line-height: normal;
    }
    .top-block__list {
      .list-item {
        padding: 0 10px;

        a {
          display: block;
          color: #333333;

          &:hover {
            color: $color-hover;
          }
        }
        .btn-filter-product {
          padding: 0;
          color: #333333;
          border: none;
          background-color: transparent;
          cursor: pointer;
          @include transition(all 0.4s ease-in-out);

          &:hover {
            color: $color-hover;
          }
        }
      }
    }
  }
  .row {
    margin: 0;
  }
  .item-col {
    padding: 0;
    border: 1px solid  $border-color;

    &:nth-child(n+2) {
      border-left: transparent;
    }
    &.column-6:nth-child(n+7),
    &.column-5:nth-child(n+6),
    &.column-4:nth-child(n+5),
    &.column-3:nth-child(n+4),
    &.column-2:nth-child(n+3),
    &.column-1:nth-child(n+2) {
        border-top: transparent;
    }
  }
  .column-5 {
    @include flex( 0 0 20% );
    max-width: 20%;
  }
  .item-product {
    height: 100%;
    text-align: center;
  }
}
.element-recently-viewed-product {
  .title-product {
    padding: 0;
    margin: 0 0 10px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5em;
    height: 3em;
    overflow: hidden;

    a {
      display: block;
    }
  }
  .site-shop__product-add-to-cart {
    .add_to_cart_button {
      color: #ffffff;

      &.loading {
        &:after {
          position: absolute;
          content: "\f013";
          top: 5px;
          left: 50%;
          font-weight: 900;
          font-family: 'Font Awesome 5 Free';
          -webkit-animation: fa-spin 2s infinite linear;
          animation: fa-spin 2s infinite linear;
        }
      }
    }
  }
  .site-shop__product--item-content {
    .price {
      color: $color-hover;
      font-size: 18px;
      font-weight: 700;

      del {
        display: inline-block;
        font-size: 12px;
        color: #666666;
      }
    }
  }
}